// 
export default class ErrorHandler {
  constructor(options) {
    const { errorStateMap } = options.errorHandler;
    this.errorStateMap = errorStateMap;
    this.errorStateMap['default'] = this.errorStateMap['default'] || '';
  }
  // 
  responseErrorStatusHandler(response, error) {
    if (response) {
      // 
      const errorStateHandler =
        this.errorStateMap['state'][response.status] || this.errorStateMap['state']['default'];
      errorStateHandler(response, error);
    } else {
      // 
      if (!window.navigator.onLine) {
        this.errorStateMap['outLine'](error);
      } else {
        //   ElMessage.error('');
        this.errorStateMap['other'](error);
      }
    }
  }
}
export const errorCallBack = (error) => {
  return Promise.reject(error);
};
